.app__about {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__profiles {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 2rem;
}

.app__profile-item {
    width: 190px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    margin: 2rem;

    img {
        width: 100%;
        max-width: 100%;
        height: 170px;
        border-radius: 15px;
        object-fit: cover;
        border: solid 5px var(--secondary-color);
    }

    @media (min-width: 50em) {
        width: 370px;
        margin: 2rem 4rem;

        img {
            height: 320px;
        }
    }
}

#about {
    .app__wrapper {
        margin: 0;
        padding: 4rem 1rem 2rem;

        .copyright {
            display: none;
        }

        @media (min-width: 50em) {
            padding: 0 2rem 4rem 2rem;
        }
    }
}
