#home {
    position: relative;
    background: url('../../assets/bgIMG.png');
    background-repeat: repeat;
    background-position: center;
    background-size: cover;

    .app__wrapper {
        padding: 0;
        .copyright {
            display: none;
        }
    }
}

.app__header {
    flex: 1;
    width: 100%;
    height: 100%;
    flex-direction: column;
    padding: 6rem 2rem 0 2rem;

    @media (min-width: 80em) {
        flex-direction: row;
        padding: 8rem 1rem 0 1rem;
    }
}

.app__header-info {
    flex: 0.65;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;

    @media (min-width: 50em) {
        margin-inline-end: 10rem;
    }
}

.app__header-badge {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    .badge-cmp,
    .tag-cmp {
        padding: 1rem 2rem;
        border: var(--white-color);
        border-radius: 15px;
        flex-direction: row;
        width: auto;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    }

    .tag-cmp {
        flex-direction: column;
        margin-top: 3rem;

        p {
            width: 100%;
            text-transform: uppercase;
            text-align: right;
        }
    }

    span {
        font-size: 2.5rem;

        @media (max-width: 50em) {
            font-size: 5rem;
        }
    }

    @media (min-width: 50em) {
        justify-content: flex-start;
        align-items: flex-start;
    }
}

.app__header-circle {
    flex: 0.75;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 100%;
    margin-top: 1rem;

    a {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background: var(--white-color);
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

        img {
            width: 60%;
            height: 60%;
            max-width: 60%;
        }
    }

    a:nth-child(1) {
        width: 50px;
        height: 50px;
    }

    a:nth-child(2) {
        width: 100px;
        height: 100px;
        margin: 1.75rem;
    }

    a:nth-child(3) {
        width: 60px;
        height: 60px;
    }

    @media (min-width: 50em) {
        a:nth-child(1) {
            width: 170px;
            height: 170px;
        }

        a:nth-child(2) {
            width: 260px;
            height: 260px;
        }

        a:nth-child(3) {
            width: 200px;
            height: 200px;
        }
    }

    @media (min-width: 80em) {
        width: 100%;
        flex-direction: column;
        flex-wrap: wrap;
        margin-left: 0;

        div {
            margin: 1rem;
        }
    }
}

.app__header-img {
    flex: 1;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    position: relative;

    img {
        width: 100%;
        object-fit: contain;
        z-index: 1;
        // max-width: 100%;
    }

    .overlay_circle {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 0;
        width: 100%;
        height: 90%;
    }

    @media (min-width: 50em) {
        margin: 2rem 0;
    }
}

.circle-cmp {
    transition: transform 125ms ease-in-out;
    &:hover {
        transform: scale(1.2);
    }
    &:focus-visible {
        outline-offset: 0.2rem;
        transform: scale(1.2);
    }
}
