.app__footer {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__footer-cards {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin: 1rem 2rem 2rem;
    flex-direction: column;
    width: 100%;

    .app__footer-card {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
        margin: 1rem 0;
        padding: 1rem;
        border-radius: 10px;
        cursor: pointer;
        background-color: #fef4f5;
        transition: all 0.3s ease-in-out;

        img {
            margin: 0 0.7rem;
        }

        img.email {
            width: 48px;
            height: 48px;
        }

        img.mobile {
            width: 40px;
            height: 80px;
        }

        p {
            font-weight: 500;
        }

        a {
            text-decoration: none;
            font-weight: 500;

            &:focus-visible {
                outline-offset: 0.5rem;
            }
        }

        &:hover {
            box-shadow: 0 0 25px var(--highlight-color);
        }
    }

    @media (min-width: 50em) {
        flex-direction: row;
    }
}

.app__footer-cards .app__footer-card:last-child {
    background-color: #f2f7fb;
}

.app__footer-cards .app__footer-card:last-child:hover {
    box-shadow: 0 0 25px var(--highlight-color);
}

.app__footer-form {
    width: 90%;
    flex-direction: column;
    margin: 1rem 2rem;

    div {
        width: 100%;
        margin: 0.75rem 0;
        border-radius: 10px;
        background-color: var(--white-color);
        transition: all 0.3s ease-in-out;

        input,
        textarea {
            width: 100%;
            padding: 0.95rem;
            border: none;
            border-radius: 7px;
            background-color: var(--lightGray-color);
            font-family: var(--font-base);
            color: var(--gray-color);
            outline: none;

            &:hover {
                box-shadow: 0 0 25px var(--primary-color);
            }
        }

        textarea {
            height: 170px;
        }
    }

    button {
        padding: 1rem 2rem;
        border-radius: 10px;
        border: none;
        background-color: var(--secondary-color);
        font-weight: 500;
        color: var(--white-color);
        outline: none;
        margin: 2rem 0 0 0;
        font-family: var(--font-base);
        cursor: pointer;

        &:hover,
        &:focus-visible {
            background-color: #bcc0df;
        }

        &:focus-visible {
            outline-offset: 0.2rem;
            outline-color: Highlight;
            outline-color: -webkit-focus-ring-color;
            outline-style: auto;
            outline-width: 1px;
        }
    }

    @media (min-width: 50em) {
        width: 70%;
        margin: 1rem 0;
    }
}

.p-error {
    color: red;
    font-size: 0.8rem;
    text-align: center;
    line-height: 1.5;

    @media (min-width: 50em) {
        font-size: 1.2rem;
    }
}

.app__form-with-error {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

textarea {
    resize: none;
}

@supports (-webkit-overflow-scrolling: touch) {
    select,
    textarea,
    input {
        font-size: 16px;
    }
}
