.app__skills {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__skills-container {
    width: 80%;
    margin-top: 3rem;
    display: flex;
    flex-direction: column;

    @media (min-width: 80em) {
        width: 100%;
        flex-direction: row;
    }
}

.app__skills-list-container {
    flex-direction: column;
}

.app__skills-list-container,
.app__skills-list {
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.app__skills-exp {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    @media (min-width: 50em) {
        margin-top: 2rem;
    }
}

.app__skills-exp-item {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 1rem 0;
}

.app__skills-exp-works {
    flex: 3;
    margin-left: 1rem;

    .app__skills-exp-work {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        margin-bottom: 1rem;
        cursor: pointer;

        h4 {
            font-weight: 500;
        }

        p {
            font-weight: 400;
            color: var(--gray-color);
            margin-top: 5px;
        }
    }

    @media (min-width: 50em) {
        margin-left: 0;
    }
}

.app__skills-exp-year {
    flex: 1;

    p {
        font-weight: 800;
        color: var(--secondary-color);
    }

    @media (min-width: 50em) {
        margin-right: 1rem;
    }
}

.app__skill-portfolio {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
}

.app__skill-filter {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin: 1rem 0 2rem;
    width: 80%;

    .app__skill-filter-item {
        flex: 1;
        padding: 0.5rem 1rem;
        border-radius: 0.5rem;
        background-color: #edf2f8;
        color: #000;
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;
        margin: 0.5rem;

        &:hover {
            background-color: var(--secondary-color);
            color: #fff;
        }

        &:focus-visible {
            outline-offset: 0.1rem;
        }

        @media (min-width: 50em) {
            padding: 1rem 2rem;
            border-radius: 0.85rem;
        }
    }

    .item-active {
        background-color: var(--secondary-color);
        color: #fff;
    }
}

.app__skill-filter-span {
    font-size: 0.3rem;
    text-align: center;
    color: var(--white-color);
    line-height: 1.5;

    @media (min-width: 50em) {
        font-size: 1rem;
    }
}

.app__skill-portfolio {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;

    .app__skill-item {
        flex-direction: column;
        text-align: center;
        margin: 1rem;
        transition: all 0.3s ease-in-out;

        div {
            width: 5rem;
            height: 5rem;
            border-radius: 50%;
            background-color: #fff;

            img {
                width: 50%;
                height: 50%;
                max-width: 50%;
            }

            &:hover {
                box-shadow: 0 0 25px #fef4f5;
            }

            p {
                font-weight: 500;
                margin-top: 0.5rem;
            }

            @media (min-width: 50em) {
                width: 8rem;
                height: 8rem;
                margin: 1rem 2rem;
                p {
                    margin-top: 1rem;
                }
            }
        }
    }
}

.popup-content {
    border-radius: 0.85rem;
    width: 80%;

    @media (min-width: 50em) {
        width: 90%;
    }
}

.popup-overlay {
    transition: opacity 250ms ease;
}

.modal {
    font-family: var(--font-base);
    font-size: 0.85rem;
    text-align: left;
    color: var(--gray-color);
    line-height: 1.5;
    padding: 1rem 2rem;
    max-height: 100vh;
    overflow-y: auto;

    > .header {
        width: 100%;
        border-bottom: 1px solid var(--secondary-color);
        text-align: center;
        padding: 5px;
        font-weight: 800;
        color: var(--black-color);
    }

    > .content {
        width: 100%;
        padding: 10px 5px;
    }

    > .close {
        cursor: pointer;
        position: absolute;
        display: block;
        right: -10px;
        top: -10px;
        background: #ffffff;
        border-radius: 50%;
        border: 1px solid var(--secondary-color);
        outline: none;
        font-size: 1rem;
        padding: 0.5rem 0.5rem;

        span {
            line-height: 0.65;
        }

        @media (min-width: 50em) {
            font-size: 2rem;
            padding: 1rem 1rem;
        }
    }

    @media (min-width: 50em) {
        font-size: 1.75rem;
    }
}

#skills {
    .app__wrapper {
        .copyright {
            display: none;
        }
    }
}